::-webkit-scrollbar {
  overflow-y: none;
}

body {
  background-color: #282c34 !important;
}


.App {
  background-color: #282c34;
  width: 100%;
  min-height: 100vh;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

nav {
  position: sticky !important;
  top: 0px;
  z-index: 13;
}